:root {
  --background-color: #fff;
  --text-color: #000;
  --link-color: #5d24f5;
  --blend-mode: screen;
}

[theme=dark] {
  --background-color: #000;
  --text-color: #fff;
  --link-color: #f1f227;
  --blend-mode: multiply;
}

body {
  background-color: var(--background-color);
  transition: background-color 0.3s;
}

body * {
  color: var(--text-color);
  transition: color 0.3s;
}

p a {
  color: var(--text-color);
}

.toggle-container {
  position: relative;
  font-size: 2rem;
  font-weight: bold;
  user-select: none;
  overflow: hidden;
  height: 3rem;
  width: 150px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.toggle-text {
  position: absolute;
  width: 100%;
  text-align: center;
  transition: transform 0.4s ease, opacity 0.4s ease;
}

.toggle-text.light {
  transform: translateY(-100%);
  opacity: 0;
}

.toggle-text.dark {
  transform: translateY(100%);
  opacity: 0;
}

[theme=light] .toggle-text.light {
  transform: translateY(0);
  opacity: 1;
}

[theme=dark] .toggle-text.dark {
  transform: translateY(0);
  opacity: 1;
}

::selection {
  color: var(--background-color);
  background-color: var(--link-color);
}